export async function getData(url = "", data = {}, withAuth = false) {
  if (Object.keys(data).length > 0) {
    url += "?" + jsonToUri(data);
  }

  let headers = {};
  if (withAuth) {
    // headers = {
    //   Accept: "application/json",
    //   Authorization: "Bearer " + localStorage.getItem("_tkn")
    // };
    url += url.indexOf("?") > -1 ? "&" : "?";
    url += "token=" + localStorage.getItem("_tkn");
  }

  const response = await fetch(url, {
    method: "GET",
    headers
  });

  return response.json();
}

export async function postData(url = "", data = {}, withAuth = false) {
  let formData = new FormData();
  for (let key in data) {
    formData.append(key, data[key]);
  }

  let headers = {};
  if (withAuth) {
    // headers = {
    //   Accept: "application/json",
    //   Authorization: "Bearer " + localStorage.getItem("_tkn")
    // };
    url += url.indexOf("?") > -1 ? "&" : "?";
    url += "token=" + localStorage.getItem("_tkn");
  }

  const response = await fetch(url, {
    method: "POST",
    body: formData,
    headers
  });

  if (response && response.ok) {
    return await response.json();
  } else {
    return response;
  }
}

export async function deleteData(url = "", data = {}, withAuth = false) {
  let formData = new FormData();
  formData.append("delete", 1);
  for (let key in data) {
    formData.append(key, data[key]);
  }

  let headers = {};
  if (withAuth) {
    // headers = {
    //   Accept: "application/json",
    //   Authorization: "Bearer " + localStorage.getItem("_tkn")
    // };
    url += url.indexOf("?") > -1 ? "&" : "?";
    url += "token=" + localStorage.getItem("_tkn");
  }

  const response = await fetch(url, {
    method: "POST",
    body: formData,
    headers
  });

  return response.json();
}

function jsonToUri(jsonData) {
  let paramArr = [];
  for (let key in jsonData) {
    if (typeof jsonData[key] === "object") {
      for (let val of jsonData[key]) {
        paramArr.push(`${key}[]=${val}`);
      }
    } else {
      paramArr.push(`${key}=${jsonData[key]}`);
    }
  }
  return paramArr.join("&");
}
