import "../styles.css";
import { useCallback, useEffect, useState } from "react";
import ExpenseList from "./ExpenseList";
import { getExpensesFromServer } from "../helpers/apiRequests";

const Expenses = ({ openForm, setFormValues }) => {
  const [refreshing, setRefreshing] = useState(true);
  const [expenses, setExpenses] = useState([]);

  const getItems = useCallback(async () => {
    // get data from server
    setRefreshing(true);
    const res = await getExpensesFromServer();
    if (res.status) {
      setExpenses(res.expenses);
    }
    setRefreshing(false);
  }, []);

  useEffect(() => {
    getItems();
  }, [getItems]);

  return (
    <div className="container">
      {/* header */}
      <div className="row my-3">
        <div className="col">
          <button
            type="button"
            className="btn btn-success btn-sm"
            onClick={openForm}
          >
            Add
          </button>
        </div>
        <div className="col d-flex align-items-center">
          <h5 className="text-center ">Expenses</h5>
        </div>
        <div className="col d-flex justify-content-end align-items-center">
          <button
            type="button"
            className="btn btn-warning btn-sm"
            onClick={getItems}
          >
            Refresh
          </button>
        </div>
      </div>

      <div className="row">
        {/* loader */}
        {refreshing && (
          <div className="col d-flex justify-content-center">
            <div className="spinner-border text-primary " role="status">
              <span className="visually-hidden">Loading...</span>
            </div>
          </div>
        )}

        {/* empty component */}
        {!refreshing && expenses.length === 0 && <div>No Items to Display</div>}

        {/* list */}
        {!refreshing && (
          <ExpenseList
            expenses={expenses}
            setFormValues={setFormValues}
            setExpenses={setExpenses}
          />
        )}
      </div>
    </div>
  );
};

export default Expenses;
