import { deleteData, getData, postData } from "./ajax";

const API_PATH = "https://rahulpr.me/expense-manager/index.php/api/";

export async function getExpensesFromServer(data) {
  return await getData(API_PATH + "expenses", data, true);
}

export async function addExpenseToServer(data) {
  return await postData(API_PATH + "expense", data, true);
}

export async function deleteExpenseFromServer(data) {
  return await deleteData(API_PATH + "expense", data, true);
}

export async function addUserToServer(data) {
  return await postData(API_PATH + "user", data);
}
