import { FiEye, FiEyeOff } from "react-icons/fi";
import { ICON_SIZE } from "../config";

const VisibilityIcon = ({ isPrivate, id }) => {
  return isPrivate === "1" ? (
    <FiEyeOff color="grey" size={ICON_SIZE} />
  ) : (
    <FiEye color="green" size={ICON_SIZE} />
  );
};

export default VisibilityIcon;
