import { useCallback } from "react";
import { FiTrash2 } from "react-icons/fi";
import { ICON_SIZE } from "../config";
import { deleteExpenseFromServer } from "../helpers/apiRequests";

const DeleteIcon = ({ id, setExpenses }) => {
  const deleteItem = useCallback(
    (id) => async () => {
      // if (window.confirm("Are you sure to delete?")) {
      // call delete api
      const res = await deleteExpenseFromServer({ id });
      if (res.status) {
        setExpenses((prev) => prev.filter((v) => v.id !== id));
      }
      // }
    },
    [setExpenses]
  );

  return <FiTrash2 onClick={deleteItem(id)} color="red" size={ICON_SIZE} />;
};

export default DeleteIcon;
