import { useCallback } from "react";
import { GoogleLogin } from "react-google-login";
import { useAuthContextProvider } from "./contexts/AuthContext";
import { addUserToServer } from "./helpers/apiRequests";

const LoginForm = () => {
  const { login, logout } = useAuthContextProvider();

  const onSuccess = useCallback(
    async (response) => {
      if (response?.profileObj) {
        const responseServer = await addUserToServer(response.profileObj);
        if (responseServer.status) {
          // login user to application
          login(responseServer.user);
        }
      }
    },
    [login]
  );

  const onFailure = (response) => {
    console.log("Error -- ", response);
    logout();
  };

  return (
    <GoogleLogin
      clientId="474519777941-480h7iifnujeofd33nu2moliisrjqtr0.apps.googleusercontent.com"
      buttonText="Login"
      onSuccess={onSuccess}
      onFailure={onFailure}
      cookiePolicy={"single_host_origin"}
      isSignedIn={true}
    />
  );
};

export default LoginForm;
