import "../styles.css";
import DeleteIcon from "./DeleteIcon";
import VisibilityIcon from "./VisibilityIcon";
import EditIcon from "./EditIcon";
import { CURRENCY } from "../config";

const ExpenseList = ({ expenses, setExpenses, setFormValues }) => {
  let total = 0;
  let myExpense = 0;
  let sharedExpense = 0;

  return (
    <div className="container">
      <table className="table  align-middle">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Expense Type</th>
            <th scope="col" className="text-end">
              Amount
            </th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {expenses.map((item, i) => {
            total += parseFloat(item.amount);
            if (item.private === "1") {
              myExpense += parseFloat(item.amount);
            } else {
              sharedExpense += parseFloat(item.amount);
            }

            return (
              <tr key={i}>
                <td>
                  <VisibilityIcon id={item.id} isPrivate={item.private} />
                </td>
                <td>
                  {item.name}
                  <br />
                  <span className="badge bg-info text-dark">
                    {item.created_at}
                  </span>
                </td>
                <td className="text-end">
                  {CURRENCY} {item.amount}
                </td>
                <td className="text-end">
                  <EditIcon item={item} setFormValues={setFormValues} />
                </td>
                <td>
                  <DeleteIcon id={item.id} setExpenses={setExpenses} />
                </td>
              </tr>
            );
          })}
        </tbody>
        <tfoot className="text-end">
          <tr className="table-warning">
            <th scope="col" colSpan={1}></th>
            <th scope="col">Shared</th>
            <th scope="col">
              {CURRENCY} {sharedExpense}
            </th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
          <tr className="table-warning">
            <th scope="col" colSpan={1}></th>
            <th scope="col">Mine</th>
            <th scope="col">
              {CURRENCY} {myExpense}
            </th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
          <tr className="table-warning">
            <th scope="col" colSpan={1}></th>
            <th scope="col">Total</th>
            <th scope="col">
              {CURRENCY} {total}
            </th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </tfoot>
      </table>
    </div>
  );
};

export default ExpenseList;
